import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { CommentsApprovedComponent } from './comments-approved.component';

const routes: Routes = [
  { path: '', component: CommentsApprovedComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CommentsApprovedRoutingModule { }



